.button {
  display: block;
  margin-top: 2rem;
  margin-bottom: 1rem;
  padding: 0.8rem 3rem;
  background-color: #1f1f1f;
  color: white;
  border-radius: 20px;
  /* width: 30%; */
  /* margin: 1rem auto; */
  font: inherit;
  border: 1px solid #4f005f;
}

.button:hover,
.button:active {
  background: #741188;
  border-color: #741188;
}

.button:focus {
  outline: none;
}
